import axios from "axios";
import {API_PATH, TOKEN_NAME} from "../../tools/constants";
import {UPDATE_STATE} from "../actionTypes/createAgentType";
import {toast} from "react-toastify";
import {CREATE_TICKET, LOGIN} from "../actionTypes/authType";



export function updateState (data){
    return {
        type: UPDATE_STATE,
        payload: data
    }
}


export function getAllTicket(Number, data , pageIndex){
    return function (dispatch){



        axios.get(  API_PATH + 'ticket/v1/list/?page=' + (Number ? Number : '1')   , {headers: {Authorization: "Bearer " + localStorage.getItem(TOKEN_NAME)}})

            .then(res =>{
                dispatch(updateState({getTickets: res.data.data}));
                dispatch(updateState({pageIndex: Number}));
                dispatch(updateState({AllCount: res.data}));

            })
            .catch(err =>{
                console.log(err)
            })




    }
}


