export function getAllTicketForSearch(data, getTickets){
    return function (dispatch){

        console.log(getTickets)

        let   newArr = getTickets.filter(item => item.subject.includes(data))

        axios.get(API_PATH + 'ticket/v1/list/' , {headers: {Authorization: "Bearer " + localStorage.getItem(TOKEN_NAME)}})

            .then(res =>{

                dispatch(updateState({getTickets:  newArr}));


            })
            .catch(err =>{
                console.log(err)
            })
    }
}







